export default class Card{
    /**
     *
     * @param {string} from Начало движения
     * @param {string} to Конец движения
     * @param {string} type Тип транспорта
     * @param {string} seat Место
     */
    constructor(from, to, type, seat){
        this.from = from;
        this.to = to;
        this.type = type;
        this.seat = seat;
    }
}